import React, {useState} from 'react';
import {access} from './access' 
import styles from './authForm.module.css';
import NavBar from '../NavBar/NavBar'
import Fade from 'react-reveal/Fade';

const AuthForm = ({allUs,allPass, onClick2, firstName, lastName,email}) => {	

	const[username, setUsername]= useState('');
	const[password, setPassword]= useState('');
	const[isLoading, setIsLoading]= useState(false); //загрузка
	const[isLoggedIn, setIsLoggedIn]= useState(false);// залогинены
	const[error, setError]= useState('');
	const[form, setForm]= useState(styles.formPassive);
	
	const onSubmit = async e => {
	e.preventDefault();
	setIsLoading(true);
	setError(''); // обнуляем форму
	
	try {
	await access(username, password,allUs,allPass);
	setIsLoggedIn(true); // меняем на залогирован=true при правильном вводе
	setError('');// обнуляем форму
	setPassword('')// обнуляем форму
	} catch (error) {
	setError('Incorrect username or password');// устанавливаем ошибку  при неправильном вводе
	}
		setIsLoading(false)	// соответственно незалогирован	
};
		
	const onLogIn = () => {
	setError('');
	setForm(styles.formActive); /*передаем как пропс далее*/
	setUsername('');
	setPassword('');
	} 

	const onLogOut = () => {
	setError('');
	setForm(styles.formActive); /*передаем как пропс далее*/
	setUsername('');
	setPassword('');
	setIsLoggedIn(false)
	}
	
	function InvalidMsg(e) { 
  
            if ((username === '')&&(password === '')) { 
                setError('Please enter username and password'); 
            } 
			else if (username === '') { 
                setError('Please enter username'); 
            } 
			else  { 
                setError('Please enter password');  
            }         
        } 
		
return (  		
<>
 {isLoggedIn ? ( //если залогирован то пишем "Хелло юзер", используем выражение в {}, т.к. вставляем JS	
		<div>
		<div className={styles.userGreeting}>
		<div>Welcome</div>
		<div>{username}!</div>
	<button   onClick={()=>setIsLoggedIn(false)}>Log Out</button>   {/*onClick меняет на "незалогирован", соответственно возращаемся в форму логина*/}
		</div>	
		
		
		</div>	) : ( 
			// <Fade left>
	<form className={form} onSubmit={onSubmit}>
	<p className={styles.formGreeting}>Login to your account</p>
	<div>
	<button className={styles.closeForm} onClick={()=>setForm(styles.formPassive) }>&times;</button>
	
	  <input className={styles.inputName}
	  type="text" 
	  placeholder="username" 
	value={username} 
	onChange={(e)=>{setUsername(e.currentTarget.value)}}// меняем username на вводимое значение
	onInvalid={InvalidMsg}
	required 
	/> </div>
	<div> 
	  <input className={styles.inputPass}
	  type="password" 
	  placeholder="password" 
		value={password} 
		onChange={(e)=>setPassword(e.currentTarget.value)}// меняем password на вводимое значение
		onInvalid={InvalidMsg}
		required
		/> </div>
		<button 
		className={styles.submit} 
		type="submit"
		>
		{isLoading ? 'Logging In ' : 'Log In'} {/* Меняет значение кнопки на загрузку*/}
		</button>
		{error && <p className={styles.error}>{error}</p>}
		</form>
		// </Fade>  
	)}
<NavBar isLoggedIn={isLoggedIn} username={username} onLogIn={onLogIn} onClick2={onClick2} onLogOut={onLogOut} firstName={firstName} lastName={lastName} email={email}/>
</>		
)
};

export default AuthForm;
